export default {
	name:'Popup',
	 props: {
		  position:{
				type: String,
				default(){
					return "middle"
				}
		  },
		},
		data() {
			return {
			   mask:false,	
			   visible:false,
			   message:'',
			   duration:2000,
			   icon:''
			}
		},
		onLoad(e) {
			console.log(e+"=============")
		},
		created(e) {
	    	console.log(e+"=============")
	    },
		methods: {
	        hide() { //显示头部
			  this.visible=false;
			  this.mask=false
	        },
			show(options){
		     this.message = typeof options === 'string' ? options : options.message;
		     this.duration=options.duration || this.duration;
			 this.icon=options.icon;
			 this.visible=true;
			 this.mask=true;
			}
		},
		 computed: {
			
        }
	}
